dropzone{
    width: 98%;
    height: 50vh;
    overflow-y: auto;
    overflow : hidden;
    font-size: 2vmin;
    word-wrap: break-word;
    display: inline-block;
    border: 5px solid rgb(0, 0, 0);
    border-radius: 10px;
    background-color: rgb(84, 160, 184);
    
}

form{
    font-size: 2vmin;
}

input{
    width: 80%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}
.ReactGridGallery_tile-viewport{
    padding: 0;
}

.ReactGridGallery_tile-description{
    border-radius: 5px;
    background-color: #a39f9f;
    padding: 0;
}


span {
    width: 80%;
}