body {
    margin: 0;
    overflow: auto;
    text-align: center;
    font-size: 3vmin;
    background-color: #b6a6a6;
  }
  
left {
  width: 49%;
  height: 100vh;
  overflow-y: auto;
  overflow : hidden;
  font-size: 16vmin;
  word-wrap: break-word;
  display: inline-block;
  border: 5px solid black;
  border-radius: 10px;
  -webkit-transition: all 0.4s ease 0s;
  -o-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
}

right {
  width: 49%;
  height: 100vh;
  overflow-y: auto;
  font-size: 2vmin;
  word-wrap: break-word;
  display: inline-block;
  border: 5px solid black;
  border-radius: 10px;
  -webkit-transition: all 0.4s ease 0s;
  -o-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
}

h1 {
  position: relative;
  padding: 0;
  margin: 0;
  font-family: "Raleway", sans-serif;
  font-weight: 300;
  font-size: 40px;
  color: #080808;
  -webkit-transition: all 0.4s ease 0s;
  -o-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
}

h1 span {
  display: block;
  font-size: 0.5em;
  line-height: 1.3;
}
h1 em {
  font-style: normal;
  font-weight: 600;
}


.header h1 {
  text-align: center;
      font-size:30px; font-weight:300; color:#222; letter-spacing:1px;
      text-transform: uppercase;
  
      display: grid;
      grid-template-columns: 1fr max-content 1fr;
      grid-template-rows: 27px 0;
      grid-gap: 20px;
      align-items: center;
  }
  
.header h1:after,.header h1:before {
    content: " ";
    display: block;
    border-bottom: 1px solid #c50000;
    border-top: 1px solid #c50000;
    height: 5px;
  background-color:#f8f8f8;
}